
import React from 'react';
import { DoubleRightOutlined } from '@ant-design/icons';
import { ReactComponent as UrbiLogo } from '../../icons/urbi.svg';
import { useAppContext } from '../../AppContext';
import { Button, Layout } from 'antd';
import MainTabs from '../maintTabs';

const { Sider } = Layout;

const siderStyle: React.CSSProperties = {
    background: 'rgb(242, 242, 242)',
    transition: '-ms-transform 150ms ease-in 0s, -webkit-transform 150ms ease-in 0s, transform 150ms ease-in 0s',
    boxShadow: 'rgba(38, 38, 38, 0.25) 1px 0px 3px 0px',
    zIndex: 1,
    padding: '4px'
};

const expandButtonStyle: React.CSSProperties = {
    width: '40px',
    borderColor: 'transparent',
    padding: '4px 8px',
    color: 'grey',
}


function SiderPanel() {
    const { actions, setActions } = useAppContext();
    const showSideBar = () => setActions({ ...actions, sideBar: false });

    return (
        <Sider trigger={null} style={siderStyle} collapsible collapsedWidth={40} collapsed={actions.sideBar} width={352}>
            {actions.sideBar ? <><UrbiLogo className='logo-short' />
                <Button ghost style={{ ...expandButtonStyle, display: actions.sideBar ? 'block' : 'none' }} onClick={showSideBar} ><DoubleRightOutlined /> </Button></> :
                <MainTabs />}
        </Sider>
    );
}

export default SiderPanel;

